<template>
  <v-container >
          <v-card>
    <v-card-title>
      <v-icon size="36" class="mx-2">mdi-twitter-retweet</v-icon>Redeposit
    </v-card-title>
    <v-card-text >
      <v-row>
        <v-col v-for="(field, ind) in filters" :key="ind">
          <component 
            :is="field.input" 
            v-bind="field.bind"
            :value="setFilterValue($route.query[field.name] || null, field)"
            @input="filter(field,$event)"
          ></component>
        </v-col>
      </v-row>
    </v-card-text>
  </v-card>


         <v-data-table
              ref="table"
              :server-items-length="serverItemsLength"
              :page="Number($route.query.page) || 1"
              :items-per-page="Number($route.query.limit) || 10"
              :sort-by="sortBy"
              :sort-desc="sortDesc"
              :loading="loading"  
              :headers="headers"
              :items="items"
              :footer-props="{'items-per-page-options': [5, 10, 20, 50]}"
              show-expand
              item-key="_id"
              @update:page="changePage"
              @update:items-per-page="changeLimit"
              @update:sort-desc="changeSort($event,'sortDesc')"
              @update:sort-by="changeSort($event,'sortBy')"
         
             >
              <!--
              -->
              <template v-slot:item.ftd.amount="{ item }" >
    
                {{  new Intl.NumberFormat('en-US', { style: 'currency', currency:  'USD',  }).format(item.ftd ? item.ftd.amount : 0) }}  
              </template>
               <template v-slot:item.total.amount="{ item }" >
    
                {{  new Intl.NumberFormat('en-US', { style: 'currency', currency:  'USD',  }).format(item.total ? item.total.amount : 0) }}  
              </template>

              <template v-slot:item.ftd.created_on="{ item }"  >
                {{ item.ftd ? dateformat(new Date(item.ftd.created_on), 'yyyy-mm-dd') : '--' }}
              </template>



              <template v-slot:expanded-item="{ headers, item }">
                <td :colspan="headers.length" class="pa-4">
                  <v-data-table :headers="dataHeaders" :items="item.deposits" >
                    <template v-slot:item.amount="{ item }" >
                        {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}  
                    </template> 
                    <template v-slot:item.created_on="{ item }"  >
                      {{ dateformat(new Date(item.created_on), 'yyyy-mm-dd') }}
                    </template>
                  </v-data-table>
                </td>
              </template> 
            </v-data-table>
      
  </v-container>
   
</template>
<script>
import { mapActions } from 'vuex'
import dateformat from 'dateformat'
import $ from 'jquery'
import debounce from 'debounce'
 import   {
  VTextField,
  // VSelect,
  // VSwitch,
} from 'vuetify/lib'

import AppDateRange from '../components/DateRange'
const defaultBind = {
  dense: true,
  hideDetails: true,
  rounded: true,
  outlined: true
}

const filters = [
  {
    input: 'v-text-field',
    name: 'tpc',
    type: 'search',
    bind: {
      ...defaultBind,
      placeholder: 'TP'
    }
  },
  {
    input: 'v-text-field',
    name: 'name',
    type: 'search',
    bind: {
      ...defaultBind,
      placeholder: 'Name'
    }
  },
  {
    input: 'app-date-range',
    name: 'ftd.created_on',
    type: 'range',
    bind: {
      ...defaultBind,
      placeholder: 'FTD Date'
    }
  } 
]


export default {
  name: 'Redeposit',
  components:{
    VTextField,
    AppDateRange
  },
  data: () => ({
    filters,
    serverItemsLength: 0,
    loading: false,
    dateformat,
    items: [],
    headers: [
      {
        text: 'TP',
        value: 'tpc',
      },
      {
        text: 'Name',
        value: 'name'
      },
      {
        text: 'FTD',
        value: 'ftd.amount'
      },
      {
        text: 'FTD Date',
        value: 'ftd.created_on'
      },
      {
        text: 'Deposits',
        value: 'total.count'
      },
      {
        text: 'Total',
        value: 'total.amount'
      }
    ],
    dataHeaders: [
      
      {
        text: 'amount',
        value: 'amount'
      },
      {
        text: 'Date',
        value: 'created_on'
      },
      {
        text: 'Owner',
        value: 'user.displayName'
      },
     
    ]
  }),
  watch:{
    $route: {
      handler: 'fetch',
      immediate:true
    }
  },
  computed: {
    sortBy () {
      const { query } = this.$route
      if (query['sortBy[]'])
        return Array.isArray(query['sortBy[]']) ? query['sortBy[]'] : [query['sortBy[]']]
      else
        return undefined
    },
    sortDesc () {
      const { query } = this.$route
      if (query['sortDesc[]'])
        return Array.isArray(query['sortDesc[]']) ? query['sortDesc[]'].map(e => eval(e)) : [eval(query['sortDesc[]'])]
      else
        return undefined
    }

  },
  methods: {
    ...mapActions({
      get: 'api/get'
    }),


    changePage (page) {
      const  query  = Object.assign({},this.$route.query) // copy the query object to avoid routing to the same route error
      if (query.page != page) {
        query.page = page
        const uri =  this.$route.path + '?' + $.param(query)
        this.$router.push(uri)
      }
    },
    changeLimit (limit) {
      const  query  = Object.assign({},this.$route.query) // copy the query object to avoid routing to the same route error
      query.page = 1
      if ( query.limit!= limit) {
        query.limit = limit
        const uri =  this.$route.path + '?' + $.param(query)
        this.$router.replace(uri)
      }
    },

    changeSort (sort, type) {
      
       // TODO: multi-sort 
      if (this.$route.query[type+'[]'])
        delete this.$route.query[type+'[]']
      
      this.$route.query[type] = sort
      

      const uri =  this.$route.path + '?' + $.param(this.$route.query)
      
      const includeBy = uri.includes('sortBy%5B%5D')
      const includeDesc = uri.includes('sortDesc%5B%5D')
      

      
      if ((includeBy && includeDesc) || (!includeBy && !includeDesc))
        this.$router.replace(uri)
       
    },
    setFilterValue (value, field) {
      const { type } = field
      if (type == 'search' && value)
        value = value.replace('~','')

      if (type == 'range' && value)
        value = value.split('|')

      return value
    },
    filter: debounce(function (field, value) {
      const { type } = field
      
      if (type == 'search')
        value = '~'+value
      
      if (type == 'gte')
        value = '>='+value

      if (type == 'lte')
        value = '<='+value

      if (type == 'range' && Array.isArray(value))
          value = value.join('|')

      const query = Object.assign({},this.$route.query) // copy the query object to avoid routing to the same route error
      
      
      if (!value && query[field.name])
          delete query[field.name]
      else
        query[field.name] = value
      
      
      const uri =  this.$route.path + '?' + $.param(query)
      this.$router.replace(uri)
    }, 500),
    async fetch () {
      this.loading= true
      const red = await this.get({ collection: 'redeposit' + window.location.search  })
      this.loading= false
      this.items = red.data.docs
      const { totalDocs  } = red.data
      this.serverItemsLength = totalDocs
      
       
    }
  } 
}
</script>